var EventUtils = {};

EventUtils.getKeyCode = function(e) {
    switch (e.which) {
        case 8: return 'backspace';
        case 13: return 'enter';
        case 16: return 'shift';
        case 17: return 'ctrl';
        case 18:
            e.preventDefault();
            return 'alt';
        case 27: return 'esc';
        case 32: return 'space';
        case 33: return 'pgup';
        case 34: return 'pgdown';
        case 37: return 'arrowleft';
        case 38: return 'arrowup';
        case 39: return 'arrowright';
        case 40: return 'arrowdown';
        case 46: return 'del';
        case 107: return '+';
        case 109: return '-';
        default:
            if ((e.which >= 65 && e.which <= 90) || (e.which >= 48 && e.which <= 57)) { // letter lowercase
                return String.fromCharCode(e.which).toLowerCase();
            } else if (e.which >= 97 && e.which <= 105) {
                return "num" + (e.which - 96);
            }
    }

    return '';
};

EventUtils.getKeyModifier = function (e) {
    var res = '';
    if (e.ctrlKey) {
        res = 'ctrl';
    }
    else if (e.altKey) {
        res = res + 'alt';
    } else if (e.shiftKey) {
        res = res + 'shift';
    }
    return res;
};

